.parent {
  @apply fixed inset-0 w-full h-full flex justify-center 
  items-center bg-black bg-opacity-40 backdrop-blur-sm animate-fade cursor-pointer;
}

.container {
  @apply relative flex flex-col w-auto h-auto bg-white p-2.5 md:p-5;
}

.img {
  @apply h-screen/75;
} 

.buttonSave {
  @apply w-11 h-11 cursor-pointer bg-white bg-opacity-80 text-3xl flex items-center justify-center
  rounded-sm hover:bg-orange-600 hover:text-white transition-colors 
  absolute top-4 right-4 md:top-8 md:right-8;
}

.buttonClose {
  @apply absolute top-0 right-0 md:-top-10 md:-right-12 w-9 h-9 text-8xl font-semibold
  flex items-center justify-center rounded-full;
}
